import React, { Component } from 'react';
import Link from 'next/link';
import Layout from '../components/Layout';

class Test extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: true,
      members: [],
    };
  }

  async componentDidMount() {
    const res = await fetch('https://api.propublica.org/congress/v1/116/house/members.json', {
      method: 'GET',
      headers: {
        'X-API-Key': 'Q38PfmdlAd0KohAiBlCESyIgGhpMpJ5bmoQFK8s3'
      }
    });
    const data = await res.json();
    this.setState({
      isLoading: false,
      members: data.results[0].members
    })
  }

  render() {
    const { isLoading, members } = this.state;
    
    return (
      <Layout>
        <h1>Members of the US House of Representatives</h1>
        <div>
          {isLoading
            ? <div>Loading</div>
            : <ul>
            {members.map(member => (
              <li key={`${member.id}`}>
                <Link href="/member/[id]" as={`/member/${member.id}`}>
                  <a>{`${member.first_name} ${member.last_name}`}</a>
                </Link>
              </li>
            ))}
          </ul>}
        </div>
      </Layout>
    );
  }
}

export default Test;